#header {
    position: relative;
}

#header nav {
    position: relative;
    padding: .5rem;
    width: 3.5rem;
    height: 100%;
    border-right: 1px solid var(--color-gray);
    color: var(--color-black);
    overflow: hidden;
    transition: var(--ms);
}

#header nav:hover {
    width: 14rem;
}

#header ul {
    padding: 0;
    margin: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: .5rem;
    list-style: none;
}

#header li {
    flex-shrink: 0;
    border-radius: .25rem;
    overflow: hidden;
    transition: var(--ms);
}

#header li:hover,
#header nav:hover li:has(.options.active) {
    background-color: var(--color-gray);
}

#header .hr {
    height: 1px;
    flex-shrink: 0;
    background-color: var(--color-gray);
}

#header .mg {
    height: 100%;
}

#header .item {
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: .5rem;
}

#header .icon {
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
}

#header .title {
    font-weight: 700;
    text-wrap: nowrap;
    transition: var(--ms);
    opacity: 0;
}

#header nav:hover .title {
    opacity: 1;
}

#header .options {
    height: 0;
    opacity: 0;
    font-size: small;
    transition: var(--ms);
}

#header nav:hover .options.active {
    height: calc(2.5rem * 1);
    opacity: 1;
}

#header .option {
    padding: .5rem .5rem .5rem 3rem;
    height: 2.5rem;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    text-wrap: nowrap;
    gap: .5rem;
}

#anchor {
    cursor: pointer;
    z-index: 1;
    position: fixed;
    right: 1.5rem;
    bottom: 1.5rem;
    width: 3rem;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 1.5rem;
    background-color: white;
    box-shadow: 0 0 .5rem var(--color-gray);
    transition: var(--ms);
}

#anchor.rotate {
    transform: rotate(180deg);
}

#notice {
    z-index: 1;
    position: absolute;
    left: calc(100% + .5rem);
    bottom: calc(4rem + 1px);
    display: flex;
    flex-direction: column;
    gap: .5rem;
}

#notice .notice {
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 1rem;
    margin: 0;
    border-radius: 1.5rem;
    border-bottom-left-radius: 0;
    background-color: white;
    box-shadow: 0 0 .5rem var(--color-gray);
    transition: var(--ms);
    animation: fadeInUp var(--ms), fadeOutUp var(--ms) calc(var(--ms) * 3);
    animation-fill-mode: forwards;
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(4rem);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeOutUp {
    from {
        opacity: 1;
        transform: translateY(0);
    }

    to {
        opacity: 0;
        transform: translateY(-100%);
    }
}