#map,
#mapDiv {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

#mapDiv {
    background-color: hsl(204, 70%, 95%);
}

#toolbar {
    z-index: 1;
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    border-top-left-radius: .5rem;
    border-top-right-radius: .5rem;
    background-color: white;
    box-shadow: 0 0 .5rem var(--color-gray);
    color: var(--color-black);
}

#toolbar .tools {
    position: relative;
    padding: .5rem;
    height: 3.5rem;
    display: flex;
    flex-direction: row;
    gap: .5rem;
}

#toolbar .hr {
    width: 1px;
    height: 100%;
    background-color: var(--color-gray);
}

#toolbar .item {
    cursor: pointer;
    width: 2.5rem;
    height: 100%;
    border-radius: .25rem;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: var(--ms);
}

#toolbar .item.active,
#toolbar .item:hover {
    background-color: var(--color-gray);
}

#toolbar .item i {
    transition: var(--ms);
}

#toolbar .item:hover i {
    transform: scale(1.25);
}

#toolbar .searchBox {
    z-index: 1;
    position: absolute;
    top: .5rem;
    left: 3.5rem;
    width: 0;
    height: calc(100% - 1rem);
    display: flex;
    flex-direction: row;
    border-radius: 0 .25rem .25rem 0;
    background-color: var(--color-gray);
    overflow: hidden;
    transition: var(--ms);
}

#toolbar .searchBox.show {
    width: calc(100% - 4rem);
}

#toolbar .search {
    all: unset;
    padding: .5rem;
    width: 100%;
}

#toolbar .search::-webkit-search-cancel-button {
    display: none;
}

#toolbar .searchX {
    cursor: pointer;
    width: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
}

#toolbar .searchData {
    display: flex;
    flex-direction: column;
    gap: .5rem;
}

#toolbar .searchData:has(.data) {
    margin: .5rem;
}

#toolbar .searchData .data {
    cursor: pointer;
    height: 2.5rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: .5rem;
    border-radius: .25rem;
    transition: var(--ms);
}

#toolbar .searchData .data:hover {
    background-color: var(--color-gray);
}

#toolbar .searchData .icon {
    width: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
}

#toolbar .searchData .addr {
    width: 100%;
    font-weight: 700;
    font-size: small;
}