#list {
    position: relative;
    padding: 1rem;
    width: 100%;
    min-height: 100%;
}

#list .list {
    margin: auto;
    width: 100%;
    max-width: 120rem;
}

#list .list {
    cursor: default;
    border-collapse: collapse;
    color: var(--color-black);
}

#list .list td {
    text-align: center;
    border: 1px solid var(--color-black);
    transition: var(--ms);
}

#list .list tbody td {
    padding: 0 .5rem;
}

#list .list tbody tr:hover td:not(:last-child),
#list .list .btn:hover {
    background-color: var(--color-gray);
}

#list .year {
    width: 3.5rem;
    font-weight: 700;
    border: none;
}

#list .list .icon,
#list .list .num {
    pointer-events: none;
}

#list .list .icon,
#list .list td:hover .num {
    display: none;
}

#list .list td:hover .icon {
    display: inline;
}